'use client';
import { usePathname } from 'next/navigation';
import {
  Drawer,
  List,
  ListItemButton,
  ListItemIcon,
  ListItemText,
} from '@mui/material';
import {
  Home,
  Search,
  BarChart,
  Timeline,
  Settings,
  Logout,
} from '@mui/icons-material';

const Sidebar = ({ open }: { open: boolean }) => {
  const pathname = usePathname(); // Highlight active page

  const menuItems = [
    { name: "Home", href: "/home", icon: <Home /> },
    { name: "Data Exploration", href: "/home/data-exploration", icon: <Search /> },
    { name: "Visualization", href: "/data-visualization", icon: <BarChart /> },
    { name: "Statistical Analysis", href: "/statistical-analysis", icon: <Timeline /> },
    { name: "Admin Panel", href: "/admin", icon: <Settings /> },
    { name: "Logout", href: "/logout", icon: <Logout />, isLogout: true },
  ];

  return (
    <Drawer
      variant="temporary"
      open={open}
      onClose={() => open = false} 
      sx={{
        width: 240,
        flexShrink: 0,
        "& .MuiDrawer-paper": {
          width: 240,
          backgroundColor: "white",
          color: "#207B6E",
          paddingTop: "64px", // Prevent shift
        },
      }}
    >
      <List>
        {menuItems.map(({ name, href, icon, isLogout }) => (
          <ListItemButton
            key={name}
            selected={pathname === href}
            sx={{
              "&.Mui-selected": { backgroundColor: "#E1F5E1" },
              "&:hover": { backgroundColor: "#f0f0f0" },
              ...(isLogout && {
                backgroundColor: "white",
                "&:hover": {
                  backgroundColor: "#ffcccc",
                  transition: "background-color 0.3s ease",
                },
              }),
            }}
          >
            <ListItemIcon sx={{ color: isLogout ? "red" : "#207B6E" }}>{icon}</ListItemIcon>
            <ListItemText primary={name} sx={{ color: isLogout ? "red" : "#207B6E" }} />
          </ListItemButton>
        ))}
      </List>
    </Drawer>
  );
};

export default Sidebar;
